/**
 * useAuth - Custom React hook for Supabase authentication and role management
 * 
 * Provides user session, role, and authentication helpers.
 * 
 * Author: Jeremiah Pegues <word@iite.bet>
 * Version: 0.1.0
 */

"use client";

import { useState, useEffect, useCallback } from "react";
import { supabase } from "@/lib/supabaseClient";

interface User {
  id: string;
  email: string | null;
  role: string | null;
  displayName: string | null;
}

export function useAuth() {
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState<boolean>(true);

  const fetchUser = useCallback(async () => {
    const session = supabase.auth.getSession();
    const { data: { session: currentSession } } = await session;
    if (currentSession?.user) {
      const { data, error } = await supabase
        .from("users")
        .select("id, email, role, display_name")
        .eq("id", currentSession.user.id)
        .single();

      if (error) {
        console.error("Error fetching user data:", error);
        setUser(null);
      } else {
        setUser({
          id: data.id,
          email: data.email,
          role: data.role,
          displayName: data.display_name,
        });
      }
    } else {
      setUser(null);
    }
    setLoading(false);
  }, []);

  useEffect(() => {
    fetchUser();

    const { data: authListener } = supabase.auth.onAuthStateChange(() => {
      fetchUser();
    });

    return () => {
      authListener?.subscription.unsubscribe();
    };
  }, [fetchUser]);

  const signIn = async (email: string, password: string) => {
    setLoading(true);
    const { error } = await supabase.auth.signInWithPassword({ email, password });
    if (error) {
      setLoading(false);
      throw error;
    }
  };

  const signOut = async () => {
    setLoading(true);
    const { error } = await supabase.auth.signOut();
    if (error) {
      setLoading(false);
      throw error;
    }
    setUser(null);
    setLoading(false);
  };

  return {
    user,
    loading,
    signIn,
    signOut,
  };
}
